'use client';

import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';

interface ProductCardProps {
  id: number;
  title: string;
  description?: string;
  price: number;
  image_url?: string;
  onEdit: () => void;
  onDelete: () => void;
}

export function ProductCard({ title, description, price, image_url, onEdit, onDelete }: ProductCardProps) {
  return (
    <Card className="overflow-hidden shadow-md transition hover:shadow-lg">
      {image_url && (
        <img
          src={image_url}
          alt={title}
          className="w-full h-40 object-contain bg-white p-2 border-b"
        />
      )}
      <CardHeader>
        <CardTitle className="text-lg font-bold">{title}</CardTitle>
      </CardHeader>
      <CardContent>
        {description && <p className="text-sm text-muted-foreground mb-2">{description}</p>}
        <p className="font-semibold text-green-600">R$ {price.toFixed(2)}</p>

        <div className="flex gap-2 mt-4">
          <button
            onClick={onEdit}
            className="px-3 py-1 border rounded cursor-pointer hover:bg-yellow-100"
          >
            Editar
          </button>
          <button
            onClick={onDelete}
            className="px-3 py-1 border rounded cursor-pointer text-red-600 hover:bg-red-100"
          >
            Excluir
          </button>
        </div>
      </CardContent>
    </Card>
  );
}
