'use client';

import { useSearchParams } from 'next/navigation';
import { useEffect, useState } from 'react';
import { SearchHeader } from '@/components/ui/searchHeader';
import { ProductCard } from '@/components/ui/productCard';
import axios from 'axios';

interface Product {
  id: number;
  title: string;
  description: string;
  price: number;
  image_url?: string;
}

export default function ProductSearchPage() {
  const searchParams = useSearchParams();
  const query = searchParams.get('search') || '';
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        setLoading(true);
        const token = localStorage.getItem('token');
        const response = await axios.get(`http://localhost:3333/products/search?query=${query}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const data = response.data;
        setProducts(data.products ? data.products : [data.product]); // suporta busca por nome ou id
      } catch (error) {
        console.error('Erro ao buscar produtos:', error);
        setProducts([]);
      } finally {
        setLoading(false);
      }
    };

    if (query.trim()) {
      fetchProducts();
    }
  }, [query]);

  const openCreateModal = () => {
    // lÃ³gica de modal
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    window.location.href = '/login';
  };

  return (
    <div className="p-4">
      <SearchHeader
        onNewProductClick={openCreateModal}
        onLogoutClick={handleLogout}
      />

      <h2 className="text-xl font-semibold mb-4">
        Resultados para: <span className="text-blue-600">{query}</span>
      </h2>

      {loading ? (
        <p>Carregando...</p>
      ) : products.length > 0 ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {products.map(product => (
            <ProductCard
              key={product.id}
              id={product.id}
              title={product.title}
              description={product.description}
              price={product.price}
              image_url={product.image_url}
            />
          ))}
        </div>
      ) : (
        <p>Nenhum produto encontrado.</p>
      )}
    </div>
  );
}
